import * as React from "react"
import { Line } from "react-chartjs-2"
import {
  Chart as ChartJS,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
} from "chart.js"

ChartJS.register(LineElement, CategoryScale, LinearScale, PointElement)

function App() {
  const data = {
    labels: ["May 11", "May 12", "May 13", "May 14", "May 15", "May 16"],
    datasets: [
      {
        data: [1, 4, 2, 4, 5, 6],
      },
    ],
  }
  //   const options = {}

  return (
    <div>
      <Line data={data}></Line>
    </div>
  )
}

export default App
